html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

svg path,
rect,
g {
  fill: inherit;
  stroke: inherit;
}

@font-face {
  font-family: "Open Sans";
  src: url("/fontsopensans.eot");
  src: url("/fontsopensans.eot#iefix") format("embedded-opentype"), url("/fontsopensans.woff2") format("woff2"), url("/fontsopensans.woff") format("woff"), url("/fontsopensans.ttf") format("truetype"), url("/fontsopensans.otf") format("opentype"), url("/fontsopensans.svg") format("svg");
  font-style: normal;
  font-weight: normal;
}

.about-us__inner {
  background-color: #fff;
  font-family: "Open Sans", Arial, sans-serif;
  font-size: 15px;
  font-weight: 400;
  font-style: normal;
  line-height: 20px;
  color: #444444;
}

.about-us__title {
  font-family: "Open Sans", Arial, sans-serif;
  font-size: 15px;
  font-weight: 400;
  font-style: normal;
  line-height: 20px;
  color: #444444;
}

.about-us__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

@media (min-resolution: 2dppx) {
  .image {
    background-image: url(image@2x.png);
  }
}

@media (min-width: 768px) {
  .break-text {
    display: none;
  }
}

.best-product__button-wrapper {
  min-width: 259px;
  width: auto;
  display: flex;
  justify-content: space-between;
}

.button {
  border: 2px solid #444444;
  font-family: "Open Sans", Arial, sans-serif;
  font-size: 17px;
  font-weight: 700;
  font-style: normal;
  line-height: 24px;
  color: #444444;
  background-color: transparent;
  text-transform: uppercase;
  padding: 10px 85px;
  width: 100%;
  display: block;
  text-align: center;
  margin: 0;
}

.button--primary {
  background-color: #63d1bb;
  font-family: "Open Sans", Arial, sans-serif;
  font-size: 19px;
  font-weight: 700;
  font-style: normal;
  line-height: 24px;
  color: #ffffff;
  text-transform: none;
  border: 0;
  padding-left: 30px;
  text-align: left;
}

.contact__description {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  width: auto;
  max-width: 320px;
  padding: 0 30px;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .container {
    width: auto;
  }
}

@media (min-width: 1150px) {
  .container {
    width: auto;
    max-width: 1150px;
  }
}

.footer-copyright {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.footer-copyright::before {
  content: "";
  position: absolute;
  width: 261px;
  height: 1px;
  top: 0;
  left: 0;
  background-color: #e5e5e5;
}

.footer-copyright__text {
  margin: 0 30px 0 0;
  padding: 0;
  font-family: "Open Sans", Arial, sans-serif;
  font-size: 19px;
  font-weight: 700;
  font-style: normal;
  line-height: 20px;
  color: #000000;
}

.impressions__blockquote {
  list-style: none;
  margin: 0;
  padding: 0;
}

.impressions__author {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input {
  background-color: darkred;
}

.input.input--type--text {
  color: #0e84b5;
  font-size: 20px;
}

.link {
  font-family: "Open Sans", Arial, sans-serif;
  font-size: 15px;
  font-weight: 400;
  font-style: normal;
  line-height: 24px;
  color: #444444;
}

.main-footer {
  background-color: #f1f1f1;
}

.main-footer:hover .main-footer__logo-svg {
  fill: #46c1ae;
}

.main-footer:active .social-link__svg {
  fill: #46c1ae;
  opacity: 0.3;
}

.main-footer__inner {
  position: relative;
  display: flex;
  flex-direction: column;
  padding-top: 25px;
}

.main-footer__logo-svg {
  display: none;
}

@media (min-width: 768px) {
  .main-footer__logo-svg {
    display: block;
  }
}

.main-header {
  background-color: red;
}

.main-logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.main-logo__svg {
  display: none;
}

@media (min-width: 1150px) {
  .main-logo__svg {
    display: block;
  }
}

.main-logo__svg--mobile {
  display: block;
}

@media (min-width: 768px) {
  .main-logo__svg--mobile {
    display: none;
  }
}

.main-logo__svg--tablet {
  display: none;
}

@media (min-width: 768px) and (max-width: 1149px) {
  .main-logo__svg--tablet {
    display: block;
  }
}

.main-page {
  padding: 0;
  margin: 0;
}

.main-navigation {
  position: relative;
}

.main-navigation__list {
  list-style: none;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", Arial, sans-serif;
  font-size: 15px;
  font-weight: 400;
  font-style: normal;
  line-height: 24px;
  color: #444444;
}

.main-navigation__link {
  font-family: "Open Sans", Arial, sans-serif;
  font-size: 17px;
  font-weight: 700;
  font-style: normal;
  line-height: 24px;
  color: #000000;
}

.main-paragraph {
  list-style: none;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", Arial, sans-serif;
  font-size: 15px;
  font-weight: 400;
  font-style: normal;
  line-height: 24px;
  color: #444444;
}

.main-title {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", Arial, sans-serif;
  font-size: 25px;
  font-weight: 700;
  font-style: normal;
  line-height: 30px;
  color: #000000;
}

.main-title--size--l {
  font-size: 27px;
  text-align: center;
  margin: 0 auto;
  width: 100%;
}

.promo-things {
  background-color: #f9f9f9;
}

.social-link {
  cursor: pointer;
  display: block;
}

.social-link:hover .social-link__svg {
  fill: #46c1ae;
}

.social-link:active .social-link__svg {
  fill: #46c1ae;
  opacity: 0.3;
}

.social-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 50px;
}

.social-list__item {
  margin-right: 41px;
  margin-bottom: 6px;
}

.social-list__item:nth-child(3n) {
  margin-right: 0;
}

.svg-image {
  margin: 0 auto;
  fill: #231F20;
}

.toggle-button {
  background-color: #ffffff;
  width: 40px;
  height: 40px;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
}
